<!--
Module: src/webview/components/AuthReminder.svelte
Owner: webview
Reads: syncState from extension (ApplicationContext serialized)
Writes: UI-only events; selection via selection writer factory (webview-owned)
Receives: syncState, host broadcasts
Emits: appEvent envelopes (Router handles stamping)
Prohibitions: Do not import extension host modules; Do not define context types
Rationale: Svelte UI component; reacts to ApplicationContext and forwards intents

LLM-GUARD:
- Use selection writer factory for selection updates
- Do not route by connection here; let Router decide targets
-->
<script lang="ts">
  interface Props {
    context: any;
    sendEvent: (event: any) => void;
  }

  const { context, sendEvent }: Props = $props();

  // Device code session reactive bindings
  const deviceCodeSession = $derived(context?.deviceCodeSession);
  const deviceCodeRemainingMs = $derived(
    deviceCodeSession ? Math.max(deviceCodeSession.expiresAt - Date.now(), 0) : 0
  );
  const deviceCodeExpiresInMinutes = $derived(
    deviceCodeSession ? Math.ceil(deviceCodeRemainingMs / 60000) : 0
  );
  const deviceCodeExpired = $derived(
    deviceCodeSession ? deviceCodeRemainingMs <= 0 : true
  );

  // Auth code flow session reactive bindings
  const authCodeFlowSession = $derived(context?.authCodeFlowSession);
  const authCodeFlowRemainingMs = $derived(
    authCodeFlowSession ? Math.max(authCodeFlowSession.expiresAt - Date.now(), 0) : 0
  );
  const authCodeFlowExpiresInMinutes = $derived(
    authCodeFlowSession ? Math.ceil(authCodeFlowRemainingMs / 60000) : 0
  );
  const authCodeFlowExpired = $derived(
    authCodeFlowSession ? authCodeFlowRemainingMs <= 0 : true
  );

  // PAT error reactive bindings
  const workItemsError = $derived(context?.workItemsError);
  const workItemsErrorConnectionId = $derived(context?.workItemsErrorConnectionId);
  const activeConnectionId = $derived(context?.activeConnectionId);
  // const workItems = $derived(context?.workItems || context?.pendingWorkItems?.workItems || []);
  // const hasWorkItems = $derived(workItems.length > 0);

  // Get connection health error from UI state
  const uiState = $derived(context?.ui);
  const connectionHealth = $derived(uiState?.connectionHealth);
  const connectionHealthError = $derived(connectionHealth?.lastError);
  const hasConnectionHealthError = $derived(
    connectionHealth?.status === 'error' &&
    connectionHealthError &&
    connectionHealthError.type === 'authentication'
  );

  // Auth reminders propagated from Praxis (serialized Map)
  const pendingAuthReminders = $derived(context?.pendingAuthReminders);
  const currentAuthReminder = $derived(
    pendingAuthReminders && activeConnectionId
      ? pendingAuthReminders instanceof Map
        ? pendingAuthReminders.get(activeConnectionId)
        : (pendingAuthReminders as Record<string, any>)[activeConnectionId]
      : undefined
  );

  // Get active connection to check auth method
  const connections = $derived(context?.connections || []);
  const activeConnection = $derived(connections.find((c: any) => c.id === activeConnectionId));
  const isEntraAuth = $derived(activeConnection?.authMethod === 'entra');

  // Check if PAT error should be shown (only if it's for the active connection)
  const showPatError = $derived(
    workItemsError && 
    workItemsErrorConnectionId === activeConnectionId &&
    !isEntraAuth // Don't show PAT error for Entra connections
  );

  // Work items auth error for Entra (when connectionHealth is absent)
  const showWorkItemsEntraAuthError = $derived(
    workItemsError &&
    workItemsErrorConnectionId === activeConnectionId &&
    isEntraAuth
  );

  // Base flags (avoid circular derivations)
  const canShowDeviceCodeBase = $derived(
    deviceCodeSession &&
      !deviceCodeExpired &&
      deviceCodeSession.connectionId === activeConnectionId
  );
  const canShowAuthCodeFlowBase = $derived(
    authCodeFlowSession &&
      !authCodeFlowExpired &&
      authCodeFlowSession.connectionId === activeConnectionId
  );
  const entraAuthErrorEligible = $derived(
    hasConnectionHealthError && !!activeConnectionId && isEntraAuth
  );

  // Final visibility flags - prioritize auth code flow > device code > error
  const showAuthCodeFlow = $derived(Boolean(canShowAuthCodeFlowBase));
  const showDeviceCode = $derived(Boolean(canShowDeviceCodeBase && !showAuthCodeFlow));
  const showEntraAuthError = $derived(Boolean(entraAuthErrorEligible && !showDeviceCode && !showAuthCodeFlow));

  // Auth reminder visibility (fallback when connectionHealth is absent)
  const showAuthReminder = $derived(
    Boolean(
      currentAuthReminder &&
      !showAuthCodeFlow &&
      !showDeviceCode &&
      !showEntraAuthError &&
      !showWorkItemsEntraAuthError &&
      !showPatError
    )
  );

  function copyAndOpenDeviceCode() {
    if (!deviceCodeSession) return;
    // Delegate browser launch + clipboard to extension (webview sandbox limitations)
    sendEvent({
      type: 'OPEN_DEVICE_CODE_BROWSER',
      connectionId: deviceCodeSession.connectionId,
    });
  }

  function copyDeviceCodeOnly() {
    if (!deviceCodeSession) return;
    sendEvent({
      type: 'COPY_DEVICE_CODE',
      connectionId: deviceCodeSession.connectionId,
    });
  }

  function handleRetry() {
    sendEvent({ type: 'REFRESH_DATA' });
  }

  function handleOpenSettings() {
    sendEvent({ type: 'OPEN_SETTINGS' });
  }

  function handleResetAuth() {
    if (activeConnectionId) {
      sendEvent({ type: 'RESET_AUTH', connectionId: activeConnectionId });
    } else {
      sendEvent({ type: 'RESET_AUTH' });
    }
  }

  function handleCancelDeviceCode() {
    if (deviceCodeSession) {
      sendEvent({
        type: 'SIGN_OUT_ENTRA',
        connectionId: deviceCodeSession.connectionId,
      });
    }
  }

  function handleOpenAuthCodeFlowBrowser() {
    if (!authCodeFlowSession) return;
    sendEvent({
      type: 'OPEN_AUTH_CODE_FLOW_BROWSER',
      connectionId: authCodeFlowSession.connectionId,
    });
  }

  function handleCancelAuthCodeFlow() {
    if (authCodeFlowSession) {
      sendEvent({
        type: 'SIGN_OUT_ENTRA',
        connectionId: authCodeFlowSession.connectionId,
      });
    }
  }

  function handleStartFreshAuth() {
    // Open setup at the auth choice step so the user can pick a new auth type
    // and acquire a fresh token (new PAT or new Entra device code flow).
    handleResetAuth();
  }

  function startDeviceCodeSignIn() {
    if (!isEntraAuth) return;

    const connectionId = deviceCodeSession?.connectionId ?? activeConnectionId;
    if (!connectionId) return;

    sendEvent({
      type: 'SIGN_IN_ENTRA',
      connectionId,
      forceInteractive: true,
    });
  }
</script>

{#if showAuthCodeFlow}
  <!-- Entra Auth Code Flow Banner -->
  <div class="auth-reminder-banner warning">
    <span class="auth-icon"></span>
    <div class="auth-message-container">
      <span class="auth-message">
        Authentication Required: Complete sign-in in your browser ({authCodeFlowExpiresInMinutes}m left)
      </span>
    </div>
    <div class="auth-actions">
      <button
        class="auth-action"
        onclick={handleOpenAuthCodeFlowBrowser}
        title="Open browser to complete sign-in"
        aria-label="Open browser to complete sign-in"
      >
        <span class="codicon"></span>
        Open browser
      </button>
      <button
        class="auth-action secondary"
        onclick={handleCancelAuthCodeFlow}
        title="Cancel authentication"
        aria-label="Cancel authentication"
      >
        <span class="codicon"></span>
        Cancel
      </button>
    </div>
  </div>
{:else if showDeviceCode}
  <!-- Entra Device Code Banner -->
  <div class="auth-reminder-banner warning">
    <span class="auth-icon"></span>
    <div class="auth-message-container">
      <span class="auth-message">
        Authentication Required: Enter code <strong>{deviceCodeSession.userCode}</strong> in your
        browser ({deviceCodeExpiresInMinutes}m left)
      </span>
    </div>
    <div class="auth-actions">
      <button
        class="auth-action"
        onclick={copyDeviceCodeOnly}
        title="Copy code"
        aria-label="Copy code"
      >
        <span class="codicon"></span>
        Copy code
      </button>
      <button
        class="auth-action"
        onclick={copyAndOpenDeviceCode}
        title="Copy code and open browser"
        aria-label="Copy code and open browser"
      >
        <span class="codicon"></span>
        Open browser
      </button>
      <button
        class="auth-action secondary"
        onclick={handleCancelDeviceCode}
        title="Cancel authentication"
        aria-label="Cancel authentication"
      >
        <span class="codicon"></span>
        Cancel
      </button>
    </div>
  </div>
{:else if showEntraAuthError}
  <!-- Entra Auth Failure Banner -->
  <div class="auth-reminder-banner error">
    <span class="auth-icon"></span>
    <div class="auth-message">
      <strong>Authentication Failed</strong>
      <span class="error-detail">
        {connectionHealthError?.message || 'Entra ID authentication failed. Start a new sign-in to choose PAT or begin device code again.'}
      </span>
      {#if connectionHealthError?.suggestedAction}
        <span class="error-hint">Suggested: {connectionHealthError.suggestedAction}</span>
      {/if}
    </div>
    <div class="auth-actions">
      <button class="auth-action" onclick={startDeviceCodeSignIn}>
        Start device code sign-in
      </button>
      <button class="auth-action" onclick={handleStartFreshAuth}>
        Change auth / start new sign-in
      </button>
      <button class="auth-action secondary" onclick={handleOpenSettings}>Settings</button>
    </div>
  </div>
{:else if showWorkItemsEntraAuthError}
  <!-- Entra Auth Failure derived from work items error -->
  <div class="auth-reminder-banner error">
    <span class="auth-icon"></span>
    <div class="auth-message">
      <strong>Authentication Failed</strong>
      <span class="error-detail">
        {workItemsError || 'Authentication failed. Start a new sign-in to choose PAT or device code.'}
      </span>
    </div>
    <div class="auth-actions">
      <button class="auth-action" onclick={startDeviceCodeSignIn}>
        Start device code sign-in
      </button>
      <button class="auth-action" onclick={handleStartFreshAuth}>
        Change auth / start new sign-in
      </button>
      <button class="auth-action secondary" onclick={handleOpenSettings}>Settings</button>
    </div>
  </div>
{:else if showPatError}
  <!-- PAT Reauth Banner -->
  <div class="auth-reminder-banner error">
    <span class="auth-icon"></span>
    <span class="auth-message">{workItemsError}</span>
    <div class="auth-actions">
      <button class="auth-action" onclick={handleRetry}>Retry</button>
      <button class="auth-action" onclick={handleStartFreshAuth}>Change auth / start new sign-in</button>
      <button class="auth-action secondary" onclick={handleOpenSettings}>Settings</button>
    </div>
  </div>
{:else if showAuthReminder}
  <!-- Auth Reminder (fallback when connectionHealth is absent) -->
  <div class="auth-reminder-banner error">
    <span class="auth-icon"></span>
    <div class="auth-message">
      <strong>Authentication Required</strong>
      <span class="error-detail">{currentAuthReminder?.reason || 'Sign in to continue.'}</span>
    </div>
    <div class="auth-actions">
      {#if isEntraAuth}
        <button class="auth-action" onclick={startDeviceCodeSignIn}>
          Start device code sign-in
        </button>
        <button class="auth-action" onclick={handleStartFreshAuth}>
          Change auth / start new sign-in
        </button>
      {:else}
        <button class="auth-action" onclick={handleRetry}>Retry</button>
      {/if}
      <button class="auth-action secondary" onclick={handleOpenSettings}>Settings</button>
    </div>
  </div>
{/if}

<style>
  .auth-reminder-banner {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 0.75rem;
    padding: 0.75rem 1rem;
    border-radius: 4px;
    margin-bottom: 1rem;
    color: var(--vscode-foreground);
    max-width: 100%;
    box-sizing: border-box;
  }

  .auth-reminder-banner.error {
    background: var(--vscode-inputValidation-errorBackground);
    border: 1px solid var(--vscode-inputValidation-errorBorder);
    color: var(--vscode-errorForeground);
  }

  .auth-reminder-banner.warning {
    background: var(--vscode-inputValidation-warningBackground);
    border: 1px solid var(--vscode-inputValidation-warningBorder);
    color: var(--vscode-foreground);
  }

  .auth-icon {
    font-size: 1.2rem;
    flex-shrink: 0;
  }

  .auth-message-container {
    flex: 1 1 0;
    min-width: 0;
    max-width: 100%;
  }

  .auth-message {
    font-size: 0.9rem;
    word-wrap: break-word;
    overflow-wrap: break-word;
  }

  div.auth-message {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
  }

  .auth-message strong {
    font-weight: 600;
    font-size: 0.95rem;
  }

  .error-detail {
    font-size: 0.85rem;
    opacity: 0.9;
  }

  .error-hint {
    font-size: 0.8rem;
    font-style: italic;
    opacity: 0.8;
    margin-top: 0.25rem;
  }

  .auth-actions {
    display: flex;
    gap: 0.5rem;
    flex-shrink: 0;
    flex-wrap: wrap;
    align-items: center;
    flex-basis: auto;
  }

  /* Force buttons to wrap below text when space is limited */
  /* Stack vertically on smaller/medium screens to prevent overflow */
  @media (max-width: 800px) {
    .auth-reminder-banner {
      flex-direction: column;
      align-items: stretch;
    }

    .auth-message-container {
      width: 100%;
      max-width: 100%;
      margin-bottom: 0.5rem;
    }

    .auth-actions {
      width: 100%;
      justify-content: flex-end;
    }
  }

  .auth-action {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 2rem;
    height: 2rem;
    padding: 0 0.6rem;
    margin: 0;
    background: transparent;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    color: var(--vscode-icon-foreground);
    transition: background-color 0.2s ease;
  }

  .auth-action:hover {
    background: var(--vscode-toolbar-hoverBackground);
  }

  .auth-action:active {
    background: var(--vscode-toolbar-activeBackground);
  }

  .auth-action .codicon {
    font-family: 'codicon';
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    line-height: 1;
    display: inline-block;
    text-decoration: none;
    text-rendering: auto;
    text-align: center;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    user-select: none;
  }

  .auth-action.secondary {
    opacity: 0.8;
  }

  .auth-action.secondary:hover {
    opacity: 1;
  }
</style>

